<template>
  <div class="login">
    <header class="footer_logo"></header>
    <div class="login_title">档案规范化管理云平台</div>
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      class="login-ruleForm"
    >
      <el-form-item prop="name">
        <el-input
          class="login_input"
          prefix-icon="el-icon-user-solid"
          v-model="ruleForm.name"
          placeholder="请输入登录名称"
        >
        </el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          class="login_input"
          prefix-icon="el-icon-lock"
          v-model="ruleForm.password"
          placeholder="请输入登录密码"
          @keyup.enter.native="goLogin()"
          show-password
        >
        </el-input>
      </el-form-item>
    </el-form>
    <el-button type="primary" class="loginBtn" @click="goLogin()"
      >登&nbsp;&nbsp;录</el-button
    >
  </div>
</template>
<script>
import API from "@/utils/api.js";
import md5 from "md5";
export default {
  props: {},
  data() {
    return {
      locationUrl: window.location.href,
      ruleForm: {
        name: "",
        password: "",
      },
      rules: {
        name: [
          {
            required: true,
            message: "请输入登录名称",
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true,
            message: "请输入登录密码",
            trigger: "blur",
          },
        ],
      },
    };
  },
  mounted() {
    if (sessionStorage.token) {
      this.$router.push("/dataShow");
    }
  },
  methods: {
    goLogin() {
      // 点击登录
      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          // 检验通过
          this.loginAsync();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    async loginAsync() {
      const { code, data, msg } = await API.loginAction({
        accountNumber: this.ruleForm.name,
        password: md5(this.ruleForm.password),
      });
      if (code == "0000") {
        sessionStorage.token = data.token;
        sessionStorage.user_id = data.userId;
        sessionStorage.user_name = data.userName;
        sessionStorage.departmentId = data.departmentId;
        sessionStorage.departmentName = data.departmentName;
        this.$router.push("/dataShow");
      } else {
        this.$message(msg);
      }
    },
  },
};
</script>
<style lang="less">
.login {
  height: 100vh;
  max-height: 100%;
  width: 100vw;
  max-width: 100%;
  box-sizing: border-box;
  background-image: url("../assets/img/app_login_bg.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  overflow: hidden;
  .login_title{
    color: #ffffff;
    font-size: .6rem;
    text-align: center;
    margin-top: 1rem;
  }
  .login_icon{
    width: 0.5rem;
    height: 0.5rem;
  }
  .login-ruleForm {
    width: 70vw;
    margin: 0 auto;
    margin-top: 18%;
  }
  .loginBtn {
    display: block!important;
    width: 70vw;
    margin: 0 auto;
    margin-top: 30px;
    background: rgba(0, 18, 78, 0.56);
    border-radius: 3px;
    color: #ffffff;
    text-align: center;
    font-size: 0.4rem;
  }
  .footer_logo{
      width: 70vw;
      height: .8rem;
      margin: 0 auto;
      margin-top: 2.2rem;
      background-image: url("../assets/img/logo.png");
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-clip: content-box;
    }
}
</style>



